// library
import React, { useState, useEffect, lazy, Suspense } from 'react';
import { Routes, Route, Link } from "react-router-dom";

// css
import './App.scss';

// componenets
import Loading from './components/Loading';
let Home = lazy(()=> import('./components/Home'));
let Party = lazy(()=> import('./components/Party'));
let Person = lazy(()=> import('./components/Person'));
let Sillok = lazy(()=> import('./components/Sillok'));
let Detail = lazy(()=> import('./components/Detail'));

function App() {
  return (
    <div className="App">
        <Nav/>
        <Suspense fallback={ <Loading />} >
          <Routes>
            <Route path="/" element={ <Home /> }></Route>
            <Route path="person" element={ <Person /> }></Route>
            <Route path="party" element={ <Party /> }></Route>
            <Route path="sillok" element={ <Sillok /> } ></Route>
            <Route path="sillok/:id" element={ <Detail /> } ></Route>
          </Routes>
        </Suspense>
    </div>
  )
}

function Nav() {
  return (
    <nav className='nav'>
      <div className='inner'>
        <img></img>
        <button className='nav__toggler' onClick={navToggler}>
          <span></span>
          <span></span>
          <span></span>
        </button>
        <div className='nav__items'>
            <ul>
              <Link to="/"><li>홈</li></Link>
              <Link to="/person"><li>인물</li></Link>
              <Link to="/party"><li>계파</li></Link>
              <Link to="/sillok"><li>충이실록</li></Link>
            </ul>
        </div>
      </div>
    </nav>
  )

  function navToggler() {
    let toggleBtn = document.querySelector('.nav__toggler');
    let toggleList = document.querySelector('.nav__items');
    toggleBtn.classList.toggle('active');
    toggleList.classList.toggle('active')
  }
}

export default App;
